"use client";

import {useForm, SubmitHandler} from "react-hook-form";
import { z } from 'zod';
import { zodResolver } from '@hookform/resolvers/zod';

// Define Zod validation schema
const schema = z.object({
  name: z.string().min(1, "Name is required"),
  email: z.string().email("Email is required").min(1, "Invalid email address"),
  message: z.string().min(1, "Message is required"),
  department: z.string().min(1,"Department is required"),
  model: z.string().min(1,"Required"),
  serialnumber: z.string().min(1,"Required"),
  date: z.string().min(1,"Required"),

});

type FormData = z.infer<typeof schema>;

const MyForm = () => {
  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm<FormData>({
    resolver: zodResolver(schema) // Integrate Zod schema with React Hook Form
  });

  const onSubmit: SubmitHandler<FormData> = (data) => {
    console.log(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div>
        <label>Name:</label>
        <input
          {...register('name')}
          placeholder="Name"
        />
        {errors.name && <p>{errors.name.message}</p>}
      </div>

      <div>
        <label>Email:</label>
        <input
          {...register('email')}
          placeholder="Email"
        />
        {errors.email && <p>{errors.email.message}</p>}
      </div>

      <div>
        <label>Message:</label>
        <textarea
          {...register('message')}
          placeholder="Message"
        />
        {errors.message && <p>{errors.message.message}</p>}
      </div>

      <div>
      <label>Department: </label>
      <select {...register('department')}>
      <option> Select One</option>
      <option value="op1">Machining</option>
      <option value="op2">Assembly</option>
      <option value="op3">Packaging</option>
      <option value="op4">Shipping</option>
      </select>
      {errors.department && <p>{errors.department.message}</p>}
      </div>

      <div>
        <label>Model:</label>
        <textarea
          {...register('model')}
          placeholder="Message"
        />
        {errors.model && <p>{errors.model.message}</p>}
      </div>

      <div>
        <label>serialNumber:</label>
        <textarea
          {...register('serialnumber')}
          placeholder="Message"
        />
        {errors.serialnumber && <p>{errors.serialnumber.message}</p>}
      </div>

      <div>
        <label>installDate:</label>
        <input type="date" {...register('date')}/>
        {errors.date && <p>{errors.date.message}</p>}
      </div>


      <button type="submit">Submit</button>
    </form>
  );
};

export default MyForm;
